namespace $.$$ {
// ============================================================================

const geoUrl = 'https://geo.baza-winner.ru/api/v2/spots.json?base_spot_code=ru-msk&type=SubwayStation&fields=guid,title,parents&parent_fields=text,type,code&parent_type_filter=SubwayLine%7CSubwaySuperStation&limit=1000'

type guids_type = {
  [code_SubwayLine: string]: {
    [code_SubwaySuperStation: string]:
      string | {
        [title: string] : string,
      },
  }
}

type guid2point_type = {
  [guid: string]: {
    station: {
      text: string,
      code: string,
    },
    line: {
      text: string,
      code: string,
      color: string,
    },
  }
}

type code2guids_type = {
  [code: string]: string[],
}

function encode(s: string) {
  return s.replace(/ru-msk-metro-/g, '$')
}

function decode(s: string) {
  return s.replace(/\$/g, 'ru-msk-metro-')
}

onmessage = function(event: any) {
  if (event.data.cmd == 'prepare') {
    let already_got = false
    const stored = event.data.guids
    if (stored)
      try {
        apply_guids(JSON.parse(decode(stored)), stored)
        already_got = true
      } catch (err) {
        console.error(err, {stored})
      }

    fetch(geoUrl, {method: 'get'})
    .then(response => response.json())
    .then(prepare_guids)
    .then((guids: guids_type) => apply_guids(guids, stored))
    .catch(err => {
      console[
        already_got ? 'warn' : 'error'
      ](
        `while ${
          already_got ? 'update' : 'prepare'
        } data for scheme metro:`,
        err,
      )
    })
  }
}

function prepare_guids(jsonData: any) {
  const guids: guids_type = {}
  const guid2spot: {[guid: string]: any} = {}
  for (const spot of jsonData.spots) {
    const guid = spot.guid
    guid2spot[guid] = spot
    let code_SubwayLine: string
    let code_SubwaySuperStation: string
    for (const parent of spot.parents) {
      if (parent.type == 'SubwayLine') {
        code_SubwayLine = parent.code
      } else if (parent.type == 'SubwaySuperStation') {
        code_SubwaySuperStation = parent.code
      }
    }
    if (!code_SubwayLine)
      $me_throw(`spot'${guid}' has no parent[.type='SubwayLine'].code`)
    if (!code_SubwaySuperStation)
      $me_throw(`spot'${guid}' has no parent[.type='SubwaySuperStation'].code`)
    if (!guids[code_SubwayLine]) {
      guids[code_SubwayLine] = {}
    }
    const val = guids[code_SubwayLine][code_SubwaySuperStation]
    if (!val) {
      guids[code_SubwayLine][code_SubwaySuperStation] = guid
    } else {
      if (typeof val == 'string')
        guids[code_SubwayLine][code_SubwaySuperStation] = {
          [guid2spot[val].title]: val
        }
      if (guids[code_SubwayLine][code_SubwaySuperStation][spot.title]) {
        console.warn(`guids[${code_SubwayLine}][${code_SubwaySuperStation}][${spot.title}] already exists`)
      } else {
        guids[code_SubwayLine][code_SubwaySuperStation][spot.title] = guid
      }
    }
  }
  return guids
}

function apply_guids(guids: guids_type, stored: string) {
  const all_points = {} as any
  const guid2point: guid2point_type = {}
  const code2guids: code2guids_type = {}
  const scheme = JSON.parse(JSON.stringify($nl_metro_data)) // https://stackoverflow.com/questions/122102/what-is-the-most-efficient-way-to-deep-clone-an-object-in-javascript
  for (const line_id in scheme) {
    if (line_id == 'settings') continue
    const line = scheme[line_id]
    const points = line.points
    const segments = line.segments
    if (points && segments)
      $me_throw(`line'${line_id}' has both .points and .segments`, line)

    if (points) {
      process_points(points, [line_id], line, line_id, all_points, guids, guid2point, code2guids)
    } else if (segments) {
      for (const segment_id in segments) {
        const segment = segments[segment_id]

        const points = segment.points
        if (points) {
          process_points(points, [segment_id, line_id], line, line_id, all_points, guids, guid2point, code2guids)
        }
      }
    }
  }

  const stored_to_be = encode(JSON.stringify(guids))
  postMessage({cmd: 'data', data: {
    scheme,
    guid2point,
    code2guids,
    guids: stored_to_be == stored ? null : stored_to_be,
  }})
}

function styleColor(style: any): string {
  const result =
    typeof style == 'string' ?
      style as string :
      (style as any).color
  return result
}

function process_points(
  points: any,
  id_context_splitted: string[],
  line: any,
  line_id: string,
  all_points: any,
  guids: guids_type,
  guid2point: guid2point_type,
  code2guids: code2guids_type,
) {
  for (const point_id in points) {
    const point = points[point_id]
    if (point.type && point.type != 'circle') continue
    const id = [point_id].concat(id_context_splitted).join('::')
    all_points[id] = point
    if (point.transit) {
      const transits =
        Array.isArray(point.transit) ?
          point.transit as string[] :
        typeof point.transit == 'string' ?
          [point.transit as string] :
          $me_throw(`${id}.transit`, point.transit) as any
      if (transits.length) {
        let transit_point_prev: any
        for (let i = 0; i < transits.length; i++) {
          const transit_point = get_point_by_id(
            transits[i],
            all_points,
            id_context_splitted,
            () => [point_id].concat(id_context_splitted).join('::'),
          )
          if (point.code && point.code != transit_point.code)
            $me_throw(`${id}.code'${point.code}' differs from ${transits[i]}.code'${transit_point.code}'`)
          if (transit_point_prev) {
            if (transit_point_prev.code != transit_point.code)
              $me_throw(`${transits[i-1]}.code'${transit_point_prev.code}' differs from ${transits[i]}.code'${transit_point.code}'`)
          }
          transit_point_prev = transit_point
        }
        if (!point.code) {
          point.code = transit_point_prev.code
        // } else {
        //   console.warn(`${id}.code may be obtained from ${transits[0]}.code`)
        }
      }
    }
    if (!point.code)
      console.error(`missing ${id}.code`, point)
    const line_codes =
      Array.isArray(line.code) ?
        line.code :
      typeof line.code == 'string' ?
        [line.code] :
      $me_throw(`${line_id}.code`, line.code) as any as string[]
    let guid_prev: string
    let line_code_prev: string
    for (const line_code of line_codes) {
      if (!guids[line_code])
        $me_throw(`no line[.code == '${line_code}']`)
      const guid = get_point_guid(guids, id_context_splitted, line_code, line_id, point, point_id)
      if (point.guid && point.guid != guid) {
        console.log({guid, line_code, 'point.code': point.code}, guids)
        console.error(`guids[${line_code_prev}][${point.code}] (${point.guid}) != guids[${line_code}][${point.code}] (${guid})`, guids)
      } else {
        point.guid = guid_prev = guid
        line_code_prev = line_code
      }
    }
    if (!point.guid) {
      console.error(`no guid provided for ${id}`, guids)
    } else {
      if (~point_id.indexOf('<'))
        console.error(`${[point_id].concat(id_context_splitted).join('::')} has '<' in id: use .label.text for this purpose`)
      guid2point[point.guid] = {
        station: {
          text: point_id,
          code: point.code,
        },
        line: {
          text: line_id,
          code: line.code,
          color: styleColor(line.style),
        },
      }
      if (!code2guids[point.code])
        code2guids[point.code] = []
      code2guids[point.code].push(point.guid)
    }
  }
}

function get_point_guid(
  guids: guids_type,
  id_context_splitted: string[],
  line_code: string,
  line_id: string,
  point: any,
  point_id: string,
) {
  let result: string
  const val = guids[line_code][point.code]
  if (typeof val == 'string') {
    result = val as string
  } else if (val) {
    if (!point.title) {
      console.error(`${[point_id].concat(id_context_splitted).join('::')} requires .title to be set`)
    } else {
      result = guids[line_code][point.code][point.title]
    }
  }
  return result
}

function get_point_id(
  point_id: string,
  all_points: {[id: string]: $me_point_intf},
  id_context_splitted: string[],
  err: () => string,
) {
  let result = point_id
  if (!all_points[point_id]) {
    const splitted_point_id = point_id.split('::')
    const point_id_extended = splitted_point_id.concat(id_context_splitted.slice(splitted_point_id.length - 1)).join('::')
    if (point_id_extended == point_id) {
      $me_throw(`all_points['${point_id}'] not found for ${err()}`, all_points)
    }
    result = point_id_extended
    if (!all_points[point_id_extended]) {
      $me_throw(`nor all_points['${point_id}'] neither all_points['${point_id_extended}'] found for ${err()}`, all_points)
    }
  }
  return result
}

function get_point_by_id(
  point_id: string,
  all_points: {[id: string]: $me_point_intf},
  id_context_splitted: string[],
  err: () => string,
) {
  return all_points[get_point_id(point_id, all_points, id_context_splitted, err)] as any
}

// ============================================================================
}
